import React, { useState, useEffect, useRef, useContext } from "react";
import {  useNavigate } from "react-router-dom";
import Axios from "axios";
// import "react-toastify/dist/ReactToastify.css";

import { Context } from "../context/Store";
import AddForm from "./catalogue/AddForm";
import ViewList from "./catalogue/ViewList";

const Catalogue = () => {
  const history = useNavigate();
  const API_URL = process.env.REACT_APP_SERVER_URL;

  const signout = () => {
    sessionStorage.removeItem('token');
    // Redirect the user to the desired page after signout
    // For example, you can redirect them to the home page
    history('/');
  };
  useEffect(() => {
    const token = sessionStorage.getItem('token');
    if (!token) {     // If there is no token, the user is not authenticated, so you can redirect them to the desired page
      history('/');
    } else {
      history('/catalogue');
    }
  }, []);
  

  // const {  accounts, ToastContainer, getShead, edit } =
  //   useContext(Context);

  return (
    <>
   
      <section className="content">
        <div className="row">
          <div className="col-xs-12">
            <AddForm />
            <ViewList />
          </div>
        </div>
      </section>
     
    </>
  );
};

export default Catalogue;
