import React from "react";

const FormField = ({ label, name, value, type, options, onChange ,error}) => {
  return (
    <div className="form-group">
      <label htmlFor={name} className="col-sm-3 control-label">
        {label}
      </label>
      <div className="col-sm-9">
        {type === "select" ? (
          <select
            required
            value={value}
            onChange={onChange}
            name={name}
            autoComplete="off"
            id={name}
            className="form-control"
          >
            <option value=""> -- Select {label} -- </option>
            {options.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        ) : (
          <input
            required
            className="form-control"
            type={type}
            value={value}
            name={name}
            placeholder={label}
            autoComplete="off"
            onChange={onChange}
            id={name}
          />
        )}
          {error && <p className="text-danger">{error}</p>} {/* Display error message */}
   
      </div>
    </div>
  );
};

export default FormField;
