
import DataTable from '../../components/DataTable'
import React, { useEffect,useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";

import {
  getAllcatalogue,
  selectData,
  deleteData,
} from "../../store/catalogueSlice";
import swal from "sweetalert";

const ViewList = () => {
  const usersString = localStorage.getItem("users");
  const users = JSON.parse(usersString);

  const dispatch = useDispatch();
  const { catalogue_list } = useSelector((state) => state.catalogue);

  const catList = useMemo(
    () => catalogue_list.filter((item) => item.brokers === users.link_id),
    [catalogue_list, users.link_id]
  );
  const handleDelete = (itemId) => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this item!",
      icon: "warning",
      buttons: ["Cancel", "Delete"],
      dangerMode: true,
    }).then((confirmDelete) => {
      if (confirmDelete) {
        dispatch(deleteData(itemId)).then(() => {
          swal({
            title: "Success",
            text: "Item deleted successfully!",
            icon: "success",
            timer: 2500,
            button: false,
          });
          dispatch(getAllcatalogue());
        });
      }
    });
  };
  const columns = [
    {
      Header: 'LOT',
      accessor: 'lot',
    },
    {
      Header: 'INVOICE',
      accessor: 'invoice',
    },
    {
      Header: 'FACTORY',
      accessor: 'factory_name',
    },
    {
      Header: 'GRADE',
      accessor: 'grade',
    },
    {
      Header: 'NET.W',
      accessor: 'net_weight',
    },
    {
      Header: 'PACKAGE',
      accessor: 'pkgs',
    },
    {
      Header: 'TOTAL',
      accessor: 'totalkg',
    },
    {
      Header: 'WAREHOUSE',
      accessor: 'warehouse_name',
    },
    {
      Header: 'CATEGORY',
      accessor: 'category',
    },

    {
      Header: 'ACTION',
      Cell: ({ row }) => (

       <>
        <button  onClick={() => dispatch(selectData(row.original.id))}
        className="btn btn-warning mr-2">Edit</button>
         <button  onClick={() => handleDelete(row.original.id)}
        className="btn btn-danger mr-1">Delete</button>
       </>
      ),
    },
    // Add more columns as needed
  ];
  useEffect(() => {
   
    dispatch(getAllcatalogue());
  }, []);
 
  
  return (
    <section className="col-lg-12 connectedSortable">
    <div className="box box-primary">
    <div className="box-header header-custom">
      <div className="row ">
        <div className="col-md-6 text-left">
          <h3 className="box-title">Detail Info</h3>
        </div>
      
      </div>
    </div>
    <DataTable columns={columns} data={catList} />
    </div>
      </section>
  )
}

export default ViewList